@layer components {
	.datagrid {
		@apply mt-6 mb-10 max-w-none w-full overflow-auto  ;
	}

	.datagrid table {
		@apply w-full;
	}

	tbody tr:hover{
		@apply bg-[#f2f2f285] transition-all ease-in-out;
	}

	th, td {
		@apply px-[18px] py-[16px];
	} 

	thead > tr > th,
	tbody > tr > td {
		@apply py-3 border-b border-b-[#ededed]
	}

	.form-control {
		@apply rounded-[20px] border-[#ededed] placeholder:text-gray-300 focus:ring-primary focus:border-primary max-w-[240px] w-full;
	}

	td > .btn-secondary{
		@apply text-white bg-gradient inline-flex items-center gap-2 flex-row-reverse rounded-full transition-all duration-300 ease-in-out px-2 py-[7px] text-xs;
	}

	td > .btn-secondary:hover{
		@apply bg-gradient--hover;
	}

	tfoot {
		@apply mb-6;
	}

	tfoot .btn {
		@apply py-2 px-2 inline-block;
	}

	tfoot .ajax {
		@apply  hover:text-primary rounded-full;
	}

	tfoot .row-grid-bottom {
		@apply relative ;
	}

	tfoot .col-per-page {
		@apply absolute top-2 right-2 ;
	}

	tfoot .col-per-page .btn-danger {
		@apply hover:text-primary;
	}

	tfoot .col-per-page select {
		@apply w-auto;
	}

	tfoot .col-items {
		@apply mt-2;
	}

	.col.pagination div .btn {
		@apply inline-block ;
	}

	tfoot .active{
		@apply bg-none text-primary underline underline-offset-2;
	}

	tfoot .active:hover {
		@apply bg-none ;
	}

	tfoot .btn-secondary {
		@apply bg-[#fff0] hover:bg-[#fff0] text-black transition-all ease-in-out;
	}

	
}


body {
	@apply relative;
 }
 
 .datepicker {
	@apply absolute bg-white shadow-[0_0_8px_rgba(0,0,0,0.15)];

 }
 
 .datepicker-dropdown::before {
	border-bottom-color: rgba(0, 0, 0, 0.075) !important;
 }
 
 .datepicker .day.today {
	background-image: linear-gradient(to right, #f6f6f6, #eeeeee);
	text-shadow: none;
 }
 
 .datepicker .day.today:hover {
	background-image: linear-gradient(to right, #f3f3f3, #e8e8e8);
	text-shadow: none;
 }
 
 .datepicker .day.active,
 .datepicker .day.active:hover {
	background-image: linear-gradient(to right, #ed7235, #e4032d);
	text-shadow: none;
	font-weight: bold;
 }
 
 .datepicker td,
 .datepicker th {
	padding: 8px 10px;
 }