@layer components {
	.btn {
		@apply px-5 py-3 uppercase font-semibold;
	}

	.btn-primary {
		@apply text-white bg-gradient inline-flex items-center gap-2 flex-row-reverse rounded-full transition-all duration-300 ease-in-out;
	}

	.btn-primary:hover {
		@apply bg-gradient--hover ;
	}

	.btn-primary-top-bar {
		@apply rounded-full bg-gradient p-2 w-6 h-6 flex items-center justify-center cursor-pointer transition-all duration-300 ease-in-out ;
	}

	.btn-primary-top-bar:hover {
		@apply bg-gradient--hover ;
	}

	.btn-primary-top-bar__secondary {
		@apply text-white bg-gradient inline-flex items-center gap-2 flex-row-reverse rounded-full px-5 py-1 cursor-pointer transition-all duration-300 ease-in-out ;
	}

	.btn-primary-top-bar__secondary:hover {
		@apply bg-gradient--hover ;
	}

	.btn-secondary {
		@apply text-white bg-black inline-flex items-center justify-center gap-2 flex-row-reverse rounded-full transition-all duration-300 ease-in-out;
	}

	.btn-secondary:hover {
		@apply bg-gray-800;
	}
	
	input[type="submit"] {
		@apply cursor-pointer;
	}

}
