@layer components {

    .c-sidebar {
        @apply hidden max-xl:fixed max-xl:z-20 bg-white dark:bg-slate-900 flex-shrink-0 xl:block xl:border-r-2 xl:border-gray-300 w-[300px] overflow-y-auto;
    }

    .c-sidebar__inner {
        @apply px-2 pb-4 xl:overflow-y-auto xl:h-[var(--app-height)];
    }


    .c-sidebar__logo {
        @apply hidden md:flex ml-3 pb-7 bg-white xl:sticky xl:top-0 pt-5 z-10;

        a {
            @apply block;
        }

        img {
            @apply w-[170px];
        }
    }

    .c-sidebar.js-sidebar-mobile-menu {
        @apply w-[300px] block top-[82px] h-[calc(var(--app-height)_-_82px)] border-r border-gray-300;

        .c-sidebar-mobile--visible {
            @apply block;
        }
    }

    .c-sidebar__container {
        @apply block;
    }

    .c-sidebar__group {
        @apply my-2;
    }

    .c-sidebar__group .c-sidebar__container {
        @apply ml-[50px];
    }

    .c-sidebar__group .c-sidebar__container .c-sidebar__item {
        @apply hover:text-primary font-normal transition ease-in-out px-0 text-gray-600 inline-block py-1; 
    }

 


    .c-sidebar__button {
        @apply px-2 py-3 bg-gray-50 rounded-lg rounded-l-none;
    }

    .c-sidebar__item {
        @apply py-2 px-3 flex xl:flex items-center [&>i]:mr-2  [&>i]:relative [&>i]:top-[-1px] hover:text-primary font-semibold transition ease-in-out;
    } 
    
    .c-sidebar__item> i {
        @apply font-light;
        }

    .c-sidebar__item--active {
        @apply text-primary #{!important};
    }

    .c-sidebar__divider {
        @apply xl:h-[1px] xl:bg-gray-200 dark:bg-slate-600 xl:my-3;
    }

}