.c-notifications-button {
    @apply bg-white w-6 bg-gradient h-6 flex justify-center items-center rounded-full relative hover:bg-red-200 transition ease-in-out;

    i {
        @apply text-white;
    }
}

.c-notifications-button:hover {
    @apply bg-gradient--hover;
}

.c-notifications__badge {
    @apply bg-black rounded-full absolute border-2 border-white top-[-3px] right-[-3px] w-[20px] h-[20px];
}

.c-notifications__badge:not(.active) {
    @apply hidden;
}

.c-notifications:not(.active) {
    @apply hidden;
}

.js-notifications-active {
    @apply bg-red-200;
}

.js-notifications__preview.active {
    @apply block;
}


.c-notifications {
    @apply absolute bg-white shadow-lg rounded-lg sm:w-full sm:max-w-md p-4 border border-primary top-[100px] max-sm:left-4 right-4 sm:right-6  max-h-[800px] h-[80vh] overflow-y-auto z-[1];
}

.c-notification {
    @apply border-b border-gray-300 pb-4 mb-4 last:border-b-0;
}

.c-notification__time {
    @apply text-primary text-sm;
}

.c-notification a {
    @apply hover:text-primary transition ease-in-out underline;
}

.c-notification__content {
    @apply mt-1 text-gray-600;
}

.c-notification__load-more {
    @apply text-center text-gray-600 hover:text-primary transition ease-in-out cursor-pointer;
}


#notifications__preview {
    position: absolute;
    background-color: white;
    padding: 10px;
    width: 420px;
    max-width: 80%;
    max-height: 80%;
    box-shadow: 0 0 2px rgba(0,0,0,0.2);
    border-radius: 5px;
    overflow-y: auto;
    right: 5px;
}

#notifications__preview:not(.active) {
    display: none;
}

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}

#notifications__loader {
    opacity: 0.25;
    padding: 0 10px;
    text-align: center;
}

#notifications__loader svg {
    animation-name: spin;
    animation-duration: 500ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    margin: 0 auto;
}


.c-notifications__loader {
    text-align: center;
}

.c-notifications__loader svg {
    display: inline-block;
    animation-name: spin;
    animation-duration: 500ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    margin: 0 auto;
    fill: none !important;
}

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}