///*------------------------------------*\
//    #MIXINS
//\*------------------------------------*/

// html element have to setup font-size: 62.5%
//
// html {
// 	font-size: 62.5%;
// }
//
// Create a fully formed type style (sizing and vertical rhythm) by passing in a
// single value, e.g.:
//
// .foo {
//     @include font-size(12px);
// }
//
// This will generate a rem-based font-size with its pixel fallback, as well as
// a unitless line-height which will place the element on your baseline, e.g.:
//
// .foo {
//     font-size: 12px;
//     font-size: 1.2rem;
//     line-height: 2;
// }
//
// If you do not want to generate a line-height automatically, you simply pass
// in your own as a second paramater, e.g.:
//
// .foo {
//     @include font-size(12px, 1.5);
// }
//
// This will yield:
//
// .foo {
//     font-size: 12px;
//     font-size: 1.2rem;
//     line-height: 1.5;
// }
//
// This parameter can be any integer, inherit, or normal.
//
// If you dont want a line-height at all, pass in a second paramater of none
// or false, e.g.:
//
// .foo {
//     @include font-size(12px, none);
// }
//
// This will yield:
//
// .foo {
//     font-size: 12px;
//     font-size: 1.2rem;
// }
//
// If you would like the rules to carry `!important`, pass in a third paramater
// of important, e.g.:
//
// .foo {
//     @include font-size(12px, none, important);
// }
//
// This will yield:
//
// .foo {
//     font-size: 12px !important;
//     font-size: 1.2rem !important;
// }



// media queries
// USAGE
// @include mq(tablet, max) {
//  CODE
// }
@mixin mq($width, $type: min) {
    @if map_has_key($breakpoints, $width) {
        $width: map_get($breakpoints, $width);
        @if $type == max {
            $width: $width - 0.2px;
            @if $width < 0 {
                @error "Media query width can't be lower than zero. Shouldn't you use `min` rather?";
            }
        }
        @media only print and (#{$type}-width: $width), only screen and (#{$type}-width: $width) {
            @content;
        }
    } @else {
        @if $type == max {
            $width: $width - 0.2px;
            @if $width < 0 {
                @error "Media query width can't be lower than zero. Shouldn't you use `min` rather?";
            }
        }
        @media only print and (#{$type}-width: $width), only screen and (#{$type}-width: $width) {
            @content;
        }
    }
}

@mixin retina() {
    @media
    only screen and (-webkit-min-device-pixel-ratio: 2),
    only screen and (   min--moz-device-pixel-ratio: 2),
    only screen and (     -o-min-device-pixel-ratio: 2/1),
    only screen and (        min-device-pixel-ratio: 2),
    only screen and (                min-resolution: 192dpi),
    only screen and (                min-resolution: 2dppx) {

        @content;

    }
}

@mixin fontFace($name, $file, $weight: normal, $style: normal){
    @font-face {
        font-family: "#{$name}";
        src: url('#{$file}.woff2') format('woff2'),
        url('#{$file}.woff') format('woff'),
        url('#{$file}.ttf') format('truetype');
        font-weight: $weight;
        font-style: $style;
        font-display: swap;
    }
}

// GRID
// for example: @include grid(16);
@mixin grid($grid-columns-number) {

    @for $i from 1 through $grid-columns-number {

        &--#{$i}-#{$grid-columns-number} {
            flex-basis: 100% / $grid-columns-number * $i;
            max-width: 100% / $grid-columns-number * $i;
        }

    }
}

// Margin map respo mixin

@mixin margin($margin, $fs-breakpoints: $breakpoints) {
    $isMap : is-map($margin);

    @if ($isMap) {
        @each $fs-breakpoint, $map in $margin {
            @if $fs-breakpoint != null {
                @if map-has-key($fs-breakpoints, $fs-breakpoint) {
                    $fs-breakpoint: map-get($fs-breakpoints, $fs-breakpoint);
                }
                @media screen and (min-width: $fs-breakpoint) {
                    @include make-margin($map);
                }
            }
        }
    }
}

@mixin make-margin($map) {
    @if type-of($map) == "list" {

        $marginTop: nth($map, 1);
        $marginBott: nth($map, 2);

        margin-top: $marginTop;
        margin-bottom: $marginBott;
    }

    @else {
        @warn "Doh! #{nth($margin,1)}.";
    }
}

@function implode($pieces, $glue: "") {
    $result: null;
    @for $i from 1 through length($pieces) {
        $piece: nth($pieces, $i);
        @if type-of($piece) == list {
            $result: unquote("#{$result}#{$glue}#{implode($piece, $glue)}");
        } @else {
            $result: unquote("#{$result}#{$glue}#{$piece}");
        }
    }
    @if $result != null {
        $result: str-slice($result, str-length($glue) + 1, -1);
    }
    @return $result;
}


@mixin xmq($min, $max) {
    @if map_has_key($breakpoints, $min) {
        $min: map_get($breakpoints, $min);
    }

    @if map_has_key($breakpoints, $max) {
        $max: map_get($breakpoints, $max);
        $max: $max - 0.2px;
    }

    @media only screen and (min-width: $min) and (max-width: $max) {
        @content;
    }
}

@mixin print() {
    @media print {
        @content;
    }
}


@function is-map($var){
    @return type-of($var) == 'map';
}
