// --------------------------------------------- //
//                                               //
//                Loki notifications             //
//                                               //
// --------------------------------------------- //



.loki-flashes-holder {
    @apply fixed top-[40px] right-1/2 translate-x-1/2 z-[1000] ;
}

.loki-flash {
    @apply text-sm text-black flex justify-center p-[20px] w-[400px] mb-[20px] rounded-full animate-[show_.4s_ease-out_forwards];
}

.loki-flash--error {
    @apply text-white bg-gradient--notification  border-[#f5c6cb];
}

.loki-flash--success {
    @apply text-[#155724] bg-[#d4edda] border-[#c3e6cb];
}

.loki-flash--warning {
    @apply text-primary bg-[#FCE6D9] border-[#F8C7AA];
}

.loki-flash--notice {
    @apply bg-blue-400 border-blue-500;
}

.loki-flash--delete {
    @apply animate-[hide_.4s_ease-out_forwards];
}

@keyframes show {
    0% {
        @apply opacity-0 translate-y-[-40px] scale-[0.95];
    }

    100% {
        @apply opacity-100 translate-y-0 scale-[1];
    }
}

@keyframes hide {
    0% {
        @apply opacity-100 translate-y-0 scale-[1];
    }

    100% {
        @apply opacity-0 translate-y-[-40px] scale-[0.95];
    }
}

@include mq(xs, max) {
    .loki-flashes-holder {
        @apply w-full px-[15px];
    }

    .loki-flash {
        @apply w-full;
    }
}

.loki-confirm {
    @apply fixed top-0 right-0 left-0 bottom-0 bg-[#14141442] z-[11] transition-all ease-in-out duration-300;
}

.loki-confirm__inner {
    @apply text-[#444444] md:text-xl inline-block xs:py-[20px] py-3 xs:px-[35px] px-4 border-[1] border-gray-200 text-center bg-white shadow-[5px_5px_30px_rgba(0,0,0,0.2)] rounded-[15px] fixed left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2;
}

.loki-confirm__buttons {
    @apply text-center mt-[20px] flex flex-row justify-evenly;
}

.loki-confirm__button-yes, .loki-confirm__button-no {
    @apply border-0 outline-0 text-white px-[15px] py-[10px] min-w-[100px] rounded-[5px] cursor-pointer;
}

.loki-confirm__button-yes {
    @apply xs:my-5 my-4 xs:mx-5 mx-3 bg-[#259c41] transition ease-in-out duration-300;
}

.loki-confirm__button-yes:hover {
    @apply bg-[#2cb74c];
}

.loki-confirm__button-no {
    @apply xs:my-5 my-4 xs:mx-5 mx-3 bg-[#c4303e] transition ease-in-out duration-300;
}

.loki-confirm__button-no:hover {
    @apply bg-[#d14552];
}
