    /* custom select */
    .c-custom-select {
        position: relative;
        border-radius: 20px;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
}

    .c-custom-select__selected {
        background-color: #fff;
        display: block;
        border: 1px solid darkgray;
        border-radius: 20px;
        cursor: pointer;
        overflow: hidden;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
}

    .c-custom-select__selected .c-custom-select__selected-item {
        border-radius: 15px;
        -webkit-border-radius: 15px;
        -moz-border-radius: 15px;
        -ms-border-radius: 15px;
        -o-border-radius: 15px;
}

    .c-custom-select__selected-item {
        padding: 12px 30px 12px 12px;
    }

    .c-custom-select--disabled .c-custom-select__selected-item {
        opacity: 0.5;
    }

    .c-custom-select__selected::after {
        content: '';
        position: absolute;
        background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyNCAyNCIgd2lkdGg9IjI0IiBoZWlnaHQ9IjI0Ij48cGF0aCBmaWxsPSJub25lIiBkPSJNMCAwaDI0djI0SDB6Ii8+PHBhdGggZD0iTTEyIDEzLjE3Mmw0Ljk1LTQuOTUgMS40MTQgMS40MTRMMTIgMTYgNS42MzYgOS42MzYgNy4wNSA4LjIyMnoiLz48L3N2Zz4=');
        background-size: 100%;
        -o-object-fit: contain;
        object-fit: contain;
        width: 20px;
        height: 20px;
        right: 5px;
        top: 50%;
        -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        z-index: 0;
    }

    .c-custom-select.opened .c-custom-select__selected::after {
        transform: translateY(-50%) rotate(180deg);
    }

    .c-custom-select__items {
        margin-top: 5px;
        border: 1px solid #EF7E3C;
        border-radius: 20px;
        position: absolute;
        z-index: 999;
        width: 100%;
        background-color: #ffffff;
        margin-bottom: 30px;
        max-height: 50vh;
        overflow: auto;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
}

    .c-custom-select__items-not-found {
        padding: 12px 30px 12px 12px;
        color: darkgray;
    }

    .c-custom-select__items-ghost {
        color: darkgray;
        padding: 5px 10px;
        font-size: 85%;
    }

    .c-custom-select__items-search {
        display: block;
        width: 100%;
        padding: 11px;
        box-sizing: border-box;
        font-size: 100%;
        border: none;
        border-bottom: 1px solid #dedede;
        border-radius: 6px 6px 0 0 !important;
    }

    .c-custom-select__items-item {
        padding: 12px 30px 12px 12px;
        cursor: pointer;
        transition: background-color .2s ease;
    }

    .c-custom-select__items-item:hover {
        background: #f7f7f7;
    }

    .c-custom-select__items-item.selected {
        background: #f0f0f0;
    }

    .c-custom-select__items-item.selected:hover {
        background: #eaeaea;
    }

    .c-custom-select__selected-item--select {
        color: rgb(0, 0, 0);
    }

    .c-custom-select--multiple .c-custom-select__selected {
     
    }

    .c-custom-select--multiple .c-custom-select__selected-item {
        display: inline-block;
        padding-right: 40px;
        position: relative;
        border: 1px solid #ececec;

    }

    .c-custom-select--multiple .c-custom-select__selected-item--select {
        border-color: transparent;
    }

    .c-custom-select--multiple .c-custom-select__selected-item-cross {
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        background: transparent;
        border: none;
        cursor: pointer;
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
}

textarea {
    height: 50.47px;
}

[type=checkbox] {
    border: solid 1px #000;
    border-radius: 0;
    width: 26px;
    height: 26px;
    color: #000000;
    background-color: #fff;
}

[type=checkbox]:checked {
    color: #000000;
    border: solid 1px #000;
    background-color: #000;
}

[type=radio]{
    border: solid 1px #000;
    width: 26px;
    height: 26px;
    color: #000;
}

[type=radio]:checked {
    color: #000;
    width: 26px;
    height: 26px;
}