:root {
	--app-height: 100%;
}

html, body {
    height: var(--app-height);
}

body {
    @apply overflow-hidden bg-white;
}

[hidden] {
	display: none !important;
}


svg {
  	display: block;
  	fill: currentColor;
}

.bg-gradient {
    background: transparent linear-gradient(-90deg, #E4032D 0%, #ED7235 100%) 0% 0% no-repeat padding-box;
}

.bg-gradient--hover {
    filter: brightness(.9);
    -webkit-filter: brightness(.9);
}

.bg-gradient--footer {
    background: transparent linear-gradient(-45deg, #E4032D 0%, #ED7235 100%) 0% 0% no-repeat padding-box;
}

.bg-gradient--notification {
    background: transparent linear-gradient(-90deg, #E4032D  0%, #ED7235  100%) 0% 0% no-repeat padding-box;

}

.js-dropdown-content--hidden {
    display: none !important;
}

.js-dropdown-button {
    transform: rotate(0);

    i {
        @apply inline-block;
    }

    @apply dark:text-white hover:text-primary dark:hover:text-primary;
}

.js-dropdown-button--rotate i {
    transform: rotate(-180deg);
}

.container--secondary {
    width: 100%
}

@media (min-width: 440px) {
    .container--secondary {
        max-width:440px
    }
}

@media (min-width: 576px) {
    .container--secondary {
        max-width:576px
    }
}

@media (min-width: 768px) {
    .container--secondary {
        max-width:768px
    }
}

@media (min-width: 992px) {
    .container--secondary {
        max-width:992px
    }
}

@media (min-width: 1200px) {
    .container--secondary {
        max-width:1200px
    }
}

@media (min-width: 1600px) {
    .container--secondary {
        max-width:1600px
    }
}

.container {
    @apply max-w-full xxl:max-w-[1500px];
    
}

.c-navigation {
    @include mq(sm, max) {
        .js-dropdown-content--hidden {
            display: none !important;
        }
     }   

     @include mq(sm, min) {
        .js-dropdown-content--hidden {
            display: flex !important;
        }
     }
    
}


.gradient-overflow {
    background: linear-gradient(90deg, rgba(15,23,42,0) 0%, rgba(15,23,42,0.5) 80%, rgba(15,23,42,1) 100%);
}

.js-pane-accordion--active {
    @apply block;
}

.js-panes-accordion-button--active {
    color: theme('colors.primary.DEFAULT') !important;
}

.rotate-icon {
    @apply rotate-180;
}

.c-canvas {
    @apply dark:bg-[linear-gradient(140deg,rgba(30,41,59,1)_25%,rgba(30,41,59,0.9037990196078431)_60%,rgba(30,41,59,0.5760679271708684)_100%),url('https://imgs.digilabs.cz/s/s/my?w=3840&h=2160&m=exact&q=90')] bg-cover bg-center
}